<template>
  <div>
    <router-link to="/" class="abs" v-show="showAbs">
      <span class="back-icon iconfont-return iconfont"></span>
    </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      <router-link to="/">
        <span class="back-icon iconfont-return iconfont"></span>
      </router-link>
      <h2 class="header-title">{{sightName}}</h2>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detailHeader',
  props: {
    sightName: String
  },
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      let scrollTop = document.documentElement.scrollTop
      if (scrollTop > 10) {
        let opacity = scrollTop / 240
        opacity = opacity > 1 ? 1 : opacity
        this.opacityStyle = {opacity}
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  .back-icon
    position: absolute
    display: block
    top 0
    left 0
    width: .72rem
    line-height: .72rem
    text-align: center;
    font-size .36rem
    color: #fff
    font-weight: 700

  .abs
    position: absolute
    width: .72rem;
    height: .72rem;
    top .1rem
    left .1rem
    display: block
    background-color: rgba(0, 0, 0, .5)
    border-radius: .36rem

  .header-fixed
    position: fixed
    z-index: 100
    width: 100%
    height $headerHeight
    top 0
    left 0
    background: $bgColor-bar
    .header-title
      margin: 0 1rem;
      line-height: $headerHeight;
      font-size: .32rem;
      text-align: center;
      color: #fff
    .back-icon
      width: $headerHeight;
      line-height: $headerHeight;
</style>



