<template>
  <div class="header">
    <div class="header-left">
      <span class="iconfont iconfont-return"></span>
    </div>
    <div class="header-input">
      <span class="iconfont iconfont-search"></span>
      <span class="title">输入城市/景点/游玩主题</span>
    </div>
    <router-link to="/city">
      <div class="header-right">
        {{this.city}}
        <span class="iconfont iconfont-trangle"></span>
      </div>
    </router-link>
  </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  .header
    display flex
    line-height .86rem
    background-color $bgColor-bar
    color #fff
    .header-left
      width .64rem
      float left
      .iconfont-return
        display block
        text-align center
        font-size .4rem
    .header-input
      flex 1
      background #fff
      border-radius .1rem
      margin-top .12rem
      height .64rem
      line-height .64rem
      margin-left .2rem
      padding-left .2rem
      color #ccc
      .title
        color #e4e7ea
    .header-right
      color #fff
      min-width 1.14rem
      padding 0 .1rem
      float right
      text-align center
      .iconfont-trangle
        font-size .2rem
</style>



