<template>
  <div>
    <div class="recommend-title">
      <img class="recommend-title-img" src="http://img1.qunarzz.com/piao/fusion/1711/89/ebc329f16c55bb02.png">
      <span class="recommend-title-desc">猜你喜欢</span>
    </div>
    <ul class="recommend-list">
      <router-link tag="li" v-for="item in recList" :key="item.id" :to="'/detail/'+item.id">
        <a href="#" class="item">
          <img :src="item.url" class="item-img">
          <div class="item-info">
            <p class="item-title">{{item.title}}</p>
            <div class="item-price">
              <span class="item-price-title">
              ￥<em>{{item.price}}</em>起
              </span>
              <span class="item-site">{{item.site}}</span>
            </div>
          </div>
        </a>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    recList: Array
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .recommend-title
    position: relative
    padding-top: .24rem
    .recommend-title-img
      margin-left .2rem
      display: inline-block
      width: .3rem
      height: .3rem
    .recommend-title-desc
      display: inline-block
      margin-left: .08rem
      height: .44rem
      color: $titleColor
      font-size: .32rem
      line-height: .44rem
  .recommend-list
    margin: 0 0 0 .2rem
    padding: 0
    li
      padding: .2rem 0
      border-bottom 1px solid #f5f5f5
    .item
      display: flex
      .item-img
        width: 2rem
        height: 2rem
      .item-info
        flex: 1
        padding-left: .22rem
        min-width 0
        .item-title
          margin-top: .26rem
          height: .44rem
          color: $titleColor
          font-size: .32rem
          line-height: .44rem
          ellipsis()
        .item-price
          position: relative
          margin-top: 0.8rem
          color: #616161
          font-size: .24rem
          line-height: .4rem
          .item-price-title
          em
            color: $priceColor
          .item-price-title
            font-size .24rem
          em
            font-size: .4rem
          .item-site
            width 5em
            float: right
            margin-right: .24rem
            ellipsis()
</style>



