<template>
  <div class="list">
    <div v-for="(ticket,index) in categoryList" :key="index" class="ticket-area">
      <h3 class="title">
        <span class="iconfont iconfont-ticket"></span>
        {{ticket.name}}
      </h3>
      <div v-if="ticket.children">
        <detail-list :categoryList="ticket.children" class="list-children"></detail-list>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detailList',
  props: {
    categoryList: Array
  }
}
</script>

<style scoped lang="stylus">
  @import "~styles/varibles.styl"
  @import "~styles/mixins.styl"
  h3
    margin 0
  .ticket-area
    background: #fff
    position: relative
    .title
      position: relative
      padding: 0 .2rem
      height: $headerHeight
      color: #333
      font-size: .32rem
      line-height: $headerHeight
      ellipsis()
      &::after
        content: ''
        position: absolute
        left 0
        bottom 0
        width: 100%
        border-bottom: 1px solid $borderColor
      .iconfont
        color: $bgColor-bar
    .price-item
      float: right
  .list-children
    padding: 0 .2rem
    .title
      font-size .3rem
      font-weight normal

</style>



