<template>
  <div>
    <div class="banner" @click="handleBannerClick">
      <img class="banner-img"
           :src="bannerImg">
      <div class="banner-info">
        <div class="banner-title">{{sightName}}</div>
        <div class="banner-video">视频<span class="iconfont iconfont-forward"></span></div>
      </div>
    </div>
    <common-fade>
      <common-gallery :galleryImgs="galleryImgs" v-show="showGallery" @close="handleGalleryClose"></common-gallery>
    </common-fade>
  </div>
</template>

<script>
import commonGallery from 'common/gallery/Gallery'
import commonFade from 'common/fade-animation/Fade'

export default {
  name: 'banner',
  props: {
    sightName: String,
    bannerImg: String,
    galleryImgs: Array
  },
  data () {
    return {
      showGallery: false
    }
  },
  methods: {
    handleBannerClick () {
      this.showGallery = true
    },
    handleGalleryClose () {
      this.showGallery = false
    }
  },
  components: {
    commonGallery,
    commonFade
  }
}
</script>

<style scoped lang="stylus">
  .banner
    position: relative
    overflow: hidden
    height: 0
    padding-bottom 55%
    margin-bottom:.2rem
    &::after
      content: ''
      position: absolute
      bottom: 0
      left: 0
      width: 100%
      height: .6rem
      background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
    .banner-img
      width: 100%
    .banner-info
      position: absolute
      z-index 1
      display: flex
      right: .2rem;
      bottom: .36rem;
      left: .2rem;
      height: .4rem;
      color: #fff
      font-size: .36rem
      .banner-title
        flex: 1
      .banner-video
        .iconfont
          margin-left .1rem
</style>



