<template>
  <div>
    <div class="search">
      <input v-model="keyWord" class="search-input" type="text" placeholder="输入城市名或拼音">
    </div>
    <swiper class="search-content" v-show="keyWord" :options="swiperOption">
      <swiper-slide class="search-item" v-for="city in list" :key="city.id">
        {{city.name}}
      </swiper-slide>
      <div class="swiper-scrollbar" slot="scrollbar"></div>
      <div class="search-item" v-show="cityFix">没有找到匹配城市</div>
    </swiper>
  </div>
</template>
<script>
import {mapMutations} from 'vuex'

let vm = null
export default {
  name: 'citySearch',
  data () {
    return {
      keyWord: '',
      list: [],
      timer: null,
      cityFix: false,
      swiperOption: {
        preventClicks: false,
        preventLinksPropagation: false,
        slidesPerView: 10,
        spaceBetween: 1,
        direction: 'vertical',
        scrollbar: {
          el: '.swiper-scrollbar',
          direction: 'vertical'
        },
        freeMode: true,
        on: {
          click: function () {
            const city = this.clickedSlide.innerText
            vm.handleCityClick(city)
          }
        }
      }
    }
  },
  created () {
    vm = this
  },
  props: {
    cityList: [Object, Array]
  },
  methods: {
    handleCityClick (city) {
      this.changeLocation(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeLocation'])
  },
  watch: {
    keyWord () {
      this.cityFix = false
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyWord) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const result = []
        for (let i in this.cityList) {
          this.cityList[i].forEach((val) => {
            if (this.keyWord.length === 1) {
              if (val.spell.charAt(0) === this.keyWord || val.name.charAt(0) === this.keyWord) {
                result.push(val)
              }
            } else {
              if (val.spell.indexOf(this.keyWord) > -1 || val.name.indexOf(this.keyWord) > -1) {
                result.push(val)
              }
            }
          })
        }
        this.list = result
        if (this.list.length === 0) {
          this.cityFix = true
        } else {
          this.cityFix = false
        }
      }, 100)
    }
  }
}
</script>

<style scoped lang="stylus">
  @import "~styles/varibles.styl"

  .search
    height: .72rem
    padding: 0 .1rem
    background: $bgColor-bar

  .search-input
    width: 100%
    height: .62rem
    line-height: .62rem
    padding: 0 .1rem
    text-align: center
    box-sizing border-box
    border-radius: .1rem

  .search-content
    z-index: 10
    overflow: hidden
    background: #eee
    position: absolute
    top 1.6rem
    bottom 0
    left 0
    right 0
    .search-item
      line-height: .68rem
      min-height: .68rem
      padding-left: .2rem
      background: #fff
      color: #666
</style>



