<template>
  <div class="container">
    <div class="img-header">
      <span class="back-icon iconfont-return iconfont" @click="handleGalleryClose"></span>
      <h3 class="title">景区图片</h3>
    </div>
    <div class="img-list">
      <img v-for="img in galleryImgs" :src="img.url" :key="img.id">
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallery',
  props: {
    galleryImgs: Array
  },
  methods: {
    handleGalleryClose () {
      this.$emit('close')
    }
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  .container
    position: fixed
    z-index: 99
    top: 0
    left: 0
    right: 0
    bottom 0
    background: $bgColor
    .img-header
      background #fff
      margin-bottom .2rem
      .title
        margin 0
        padding .2rem 0
        text-align center
      .back-icon
        position: absolute
        top: .1rem
        left: .1rem
        font-size: .48rem
    .img-list
      img
        display: block
        width: 50%;
        float: left;
        margin-bottom: .1rem
        box-sizing border-box
      img:nth-child(2n+1)
        padding-left: .2rem
        padding-right: .05rem
      img:nth-child(2n)
        padding-left: .05rem
        padding-right: .2rem
</style>



