<template>
    <div class="weekend">
      <div class="weekend-title">周末去哪</div>
      <ul class="weekend-list">
        <li v-for="item in weekendList" :key="item.id">
          <a href="#">
            <div class="img-wrapper">
              <img :src="item.url" class="item-img">
            </div>
            <div class="item-info">
              <p class="item-title">{{item.title}}</p>
              <p class="item-desc">{{item.desc}}</p>
            </div>
          </a>
        </li>
      </ul>
    </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: Array
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'

  .weekend
    .weekend-title
      line-height: .8rem
      text-indent: .2rem
      background: $intervalColor
    .weekend-list
      padding:0
      margin:0
    .img-wrapper
      height:0
      padding-bottom: 37.74%
      overflow: hidden
      .item-img
        width: 100%
    .item-info
      padding: 0 .2rem .2rem
      .item-title
        color: $titleColor
        font-size .28rem
      .item-desc
        color: $descColor
        font-size .24rem
</style>



