<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page,index) in pages" :key="index">
        <div class="icon" v-for="item in page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl">
          </div>
          <p class="icon-desc" v-text="item.desc"></p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  computed: {
    pages () {
      const pages = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style scoped lang="stylus">
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'

  .icons >>> .swiper-pagination-bullet-active
    background-color #75aeff
  .icons >>> .swiper-container
    height 0
    padding-bottom 50%
  .icon
    background-color #fff
    position: relative
    height 0
    float: left
    overflow hidden
    width 25%
    padding-bottom 25%
    .icon-img
      position absolute
      top 0
      left: 0
      right: 0
      bottom .44rem
      padding .15rem
      .icon-img-content
        display block
        margin 0 auto
        height: 100%
    .icon-desc
      width 100%
      position absolute
      left: 0
      bottom 0
      height .44rem
      line-height .44rem
      text-align center
      margin 0
      color $darkTextColor
      ellipsis()
</style>



