<template>
  <div>
    <div class="area">
      <h2 class="title interval">热门城市</h2>
      <ul class="list hot-cities">
        <li v-for="city in hotCities" :key="city.id" @click="handleCityClick(city.name)">{{city.name}}</li>
      </ul>
    </div>
    <div class="area">
      <h2 class="title interval">字母排序</h2>
      <ul class="list letter-list">
        <li v-for="(letter, initial) in cityList" :key="letter.id">
          <a :href="'#letter-'+initial">{{initial}}</a>
        </li>
      </ul>
    </div>
    <div class="area" v-for="(letter, initial) in cityList" :key="letter.id">
      <a :id="'letter-'+initial"></a>
      <h2 class="title interval">{{initial}}</h2>
      <ul class=" list detail-list">
        <li v-for="city in letter" :key="city.id" @click="handleCityClick(city.name)">{{city.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import {mapState, mapMutations} from 'vuex'

export default {
  name: 'list',
  props: {
    cityList: [Array, Object],
    hotCities: Array
  },
  methods: {
    handleCityClick (city) {
      // this.$store.dispatch('changeLocation', city)
      this.changeLocation(city)
      this.$router.push('/')
    },
    ...mapMutations(['changeLocation'])
  },
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style scoped lang="stylus">
  @import "~styles/varibles.styl"
  @import "~styles/mixins.styl"
  .list
    position: relative
    color: $titleColor
    margin 0
    padding 0
    overflow: hidden
    & li
      float: left
      text-align: center
      font-size: .28rem
      line-height: .9rem
      height: .9rem
      margin-bottom: -1px;

  .title
    color: #212121
    font-size .24rem
    font-weight normal
    padding: .24rem .3rem
    margin 0

  .hot-cities
    margin 0
    padding 0
    overflow: hidden
    boderBefore(33.3%, false)
    & li
      position: relative
      z-index 1
      width: 33.33%
      border-bottom: .02rem solid $borderColor

  .letter-list
    padding: .3rem 0
    & li
      width: 16.66%
      float: left
      & a
        display: block

  .detail-list
    boderBefore(25%, 25%)
    & li
      position: relative
      z-index 1
      width: 25%
      border-bottom: .02rem solid $borderColor
</style>



