<template>
  <div class="city">
    <city-header></city-header>
    <city-search :cityList="cityList"></city-search>
    <city-list :hotCities="hotCities" :cityList="cityList"></city-list>
  </div>
</template>

<script>
import cityHeader from './components/header'
import citySearch from './components/search'
import cityList from './components/list'
import axios from 'axios'

export default {
  name: 'City',
  data () {
    return {
      hotCities: [],
      cityList: []
    }
  },
  components: {
    cityHeader,
    citySearch,
    cityList
  },
  methods: {
    getCityList () {
      axios.get('/api/city.json')
        .then(this.getCityListSucc)
    },
    getCityListSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.hotCities = data.hotCities
        this.cityList = data.cities
      }
    }
  },
  mounted () {
    this.getCityList()
  }
}
</script>

<style lang="stylus">
</style>



